@import url('https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Anton&family=BIZ+UDPGothic&family=Bebas+Neue&family=Fjalla+One&family=Josefin+Sans&family=Josefin+Slab:wght@100&family=Merriweather:wght@300&family=Montserrat:wght@200;500&family=Prompt:wght@500&family=Raleway:wght@100;200&family=Roboto&family=Roboto+Condensed&family=Rubik+Mono+One&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* Root variables = all project */

:root {
    --color-bg: #f8f8f6;
    --color-primary: #322f2f;
    --color-black: #000000;
    --color-light: #302e2e;

    --transition: all 400ms ease;

    --container-width-lg: 90%;
    --container-width-md: 86%;
    --container-width-sm: 75%;

}

/* Scroll comportement */
html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family:'Montserrat', sans-serif;
    background: var(--color-bg);
    color: var(--color-black);
    line-height: 1.7;
}

/* General style */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1 {
    font-size: 2.5rem;
}

h5 {
    font-size: 1.5rem;
}



section {
    margin-top: 8rem;
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
}

section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
   transition: var(--transition);
}

a:hover {
    color: var(--color-bg-variant);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border: 2px solid var(--color-primary);
    transition: var(--transition);
}

 .btn:hover {
     background: var(--color-black);
     color: var(--color-bg);
     border-color: transparent;
     box-shadow: -12px -12px    #a7bed3 ;

 }

 .btn-primary {
     background: var(--color-primary);
     color: var(--color-bg);
 }

 .btn-primary:hover {
    background: var(--color-bg);
    color: var(--color-black);
    border-color: var(--color-black); 
    box-shadow: 12px -12px  #a7bed3 ;

 }

 img {
     display: block;
     width: 100%;
     object-fit: cover;
 }

/* MEDIA QUERIES (Medium) */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section{
        margin-top: 6rem;
    }
}

/* MEDIA QUERIES (Small) */

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2{
        margin-top: 2rem;
    }
}

